import "./dashboard.css";
import Navbar from "././../../components/navbar/navbar"
import {
  Button,
  Grid,
  TextField,
  Typography,
  Stack,
  
} from "@mui/material";

import Dropdown, { DropdownLocation, DropdownStatus } from "../../components/dropdown/dropdown";

export function Dashboard() {
  return (
    <Grid container alignContent="center" justifyContent="center">
      <Grid item lg={12}>
        <Navbar />
      </Grid>
      <Grid
        item
        lg={6}
        className="align"
        alignContent="center"
        justifyContent="center"
        marginLeft="40px"
      >
        <Stack direction="row" pb={1}>
          <Typography variant="h6" sx={{ fontFamily: "Instrument Sans" }}>
            Connection
          </Typography>
        </Stack>
        <TextField
          fullWidth
          size="small"
          label="find by connection id"
          type="search"
          InputProps={{
            classes: {
              notchedOutline: "input-border",
            },
          }}
          InputLabelProps={{
            style: {
              color: "grey",
              fontSize: "15px",
              fontFamily: "Instrument Sans",
              border: "1px 1px grey",
            },
          }}
        />
        <Button className="center" variant="outlined">
       
          Search
        </Button>
      </Grid>

      <Grid
        item
        lg={6}
        className="align1"
        alignContent="center"
        justifyContent="center"
        marginRight="40px"
      >
        <Stack direction="row" pb={1}>
          <Typography variant="h6" sx={{ fontFamily: "Instrument Sans" }}>
            Ticket
          </Typography>
        </Stack>
        <TextField
          fullWidth
          size="small"
          label="find by user uuid"
          type="search"
        />
        <Button className="center1" variant="outlined">
          Search
        </Button>
      </Grid>

      <Grid
        item
        lg={6}
        className="align2"
        alignContent="center"
        justifyContent="center"
        marginLeft="40px"
      >
        <Stack direction="row" pb={1}>
          <Typography variant="h6" sx={{ fontFamily: "Instrument Sans" }}>
            Portals
          </Typography>
        </Stack>
        <TextField
          fullWidth
          size="small"
          label="search portals..."
          type="search"
          InputProps={{
            classes: {
              notchedOutline: "input-border",
            },
          }}
          InputLabelProps={{
            style: {
              color: "grey",
              fontSize: "15px",
              fontFamily: "Instrument Sans",
              border: "1px 1px grey",
            },
          }}
        />
        <TextField
        sx={{marginTop : "12px"}}
          fullWidth
          size="small"
          label="search portals by URL..."
          type="search"
          InputProps={{
            classes: {
              notchedOutline: "input-border",
            },
          }}
          InputLabelProps={{
            style: {
              color: "grey",
              fontSize: "15px",
              fontFamily: "Instrument Sans",
              border: "1px 1px grey",
            },
          }}
        />
        <Grid>
          <Dropdown/>
          <DropdownStatus/>
        </Grid>
        <Button className="center2" variant="outlined">
          Search
        </Button>
      </Grid>
      <Grid
        item
        lg={6}
        className="align3"
        alignContent="center"
        justifyContent="center"
        marginLeft="40px"
      >
        <Stack direction="row" pb={1}>
          <Typography variant="h6" sx={{ fontFamily: "Instrument Sans" }}>
           Directory
          </Typography>
        </Stack>
        <TextField
          fullWidth
          size="small"
          label="search portals..."
          type="search"
          InputProps={{
            classes: {
              notchedOutline: "input-border",
            },
          }}
          InputLabelProps={{
            style: {
              color: "grey",
              fontSize: "15px",
              fontFamily: "Instrument Sans",
              border: "1px 1px grey",
            },
          }}
        />
        <TextField
        sx={{marginTop : "12px" , position :'relative',right : '138px'}}
          
          size="small"
          label="zip code"
          type="search"
          InputProps={{
            classes: {
              notchedOutline: "input-border",
            },
          }}
          InputLabelProps={{
            style: {
              color: "grey",
              fontSize: "15px",
              fontFamily: "Instrument Sans",
              border: "1px 1px grey",
            },
          }}
        />
        <Grid>
          <DropdownLocation/>
        </Grid>
        <Button className="center3" variant="outlined">
          Search
        </Button>
      </Grid>
    </Grid>
  );
}
