
import { Button, Grid } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';

const columns = [
  { field: 'id', headerName: 'ID', width: 150 },
  { field: 'firstName', headerName: 'PORTLS', width: 500 },
  { field: 'lastName', headerName: 'CREATE DATE', width: 250 },
  {
    field: 'action',
    headerName: 'Action',
    width: 180,
    sortable: false,
    disableClickEventBubbling: true,

    renderCell: () =>
    <Grid>
    <Button variant='outlined' sx={{marginRight : '10px'}} onClick={() => editPortal()}>
        EDIT
    </Button>
    <Button variant='outlined' onClick={() => mergePortal()}>
        MERGE
    </Button>

    </Grid>
   
   

  }
]  
const rows = [
  { id: 1, lastName: 'Snow', firstName: 'Jon', age: 35 },
  { id: 2, lastName: 'Lannister', firstName: 'Cersei', age: 42 },
  { id: 3, lastName: 'Lannister', firstName: 'Jaime', age: 45 },
  { id: 4, lastName: 'Stark', firstName: 'Arya', age: 16 },
  { id: 5, lastName: 'Targaryen', firstName: 'Daenerys', age: null },
  { id: 6, lastName: 'Melisandre', firstName: null, age: 150 },
  { id: 7, lastName: 'Clifford', firstName: 'Ferrara', age: 44 },
  { id: 8, lastName: 'Frances', firstName: 'Rossini', age: 36 },
  { id: 9, lastName: 'Roxie', firstName: 'Harvey', age: 65 },
];

export default function DataTable() {
  return (
    <Grid style={{ height: 400, width: '100%' , position :'relative', top : '150px'}}>
      <DataGrid
        rows={rows}
        columns={columns}
       
        initialState={{
          pagination: {
            paginationModel: { page: 0, pageSize: 5 },
          },
        }}
        pageSizeOptions={[5, 10]}
        // checkboxSelection
      />
    </Grid>
  );
}
